"use client";

import { useState } from 'react';
import Link from 'next/link';
import { supabase } from '@/lib/supabase';
import { motion, AnimatePresence } from 'framer-motion';

export default function ResetPasswordPage() {
  const [email, setEmail] = useState("");
  const [status, setStatus] = useState<{ text: string; type: 'success' | 'error' | 'loading' | null }>({
    text: '',
    type: null,
  });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!email.endsWith('@u.tsukuba.ac.jp')) {
      setStatus({ text: '筑波大学のメールアドレス (@u.tsukuba.ac.jp) のみを指定可能です', type: 'error' });
      return;
    }

    setStatus({ text: '照合中...', type: 'loading' });

    // パスワードリセットメールの送信
    const { error } = await supabase.auth.resetPasswordForEmail(email, {
      redirectTo: `${window.location.origin}/update-password`,
    });

    if (error) {
      setStatus({ text: '送信失敗: ' + error.message, type: 'error' });
    } else {
      setStatus({ 
        text: 'リセット用のリンクを送信しました。メールボックスをご確認ください。', 
        type: 'success' 
      });
      setEmail("");
    }
  };

  return (
    <>
      <style jsx global>{`
        @import url('https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;700&family=Noto+Sans+JP:wght@300;400;500;700&display=swap');
        body { font-family: 'Noto Sans JP', 'Manrope', sans-serif; background-color: #F8FAFC; color: #334155; }
        
        @keyframes blob {
          0% { transform: translate(0px, 0px) scale(1); }
          33% { transform: translate(30px, -50px) scale(1.1); }
          66% { transform: translate(-20px, 20px) scale(0.9); }
          100% { transform: translate(0px, 0px) scale(1); }
        }
        .animate-blob { animation: blob 7s infinite; }
        .animation-delay-2000 { animation-delay: 2s; }
      `}</style>

      <div className="min-h-screen relative flex items-center justify-center p-4 overflow-hidden selection:bg-[#85BCC7] selection:text-white">
        
        {/* 背景装飾 */}
        <div className="fixed inset-0 z-0 pointer-events-none">
          <div className="absolute inset-0 opacity-[0.03]" style={{ backgroundImage: `url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E")` }}></div>
          <div className="absolute top-1/4 left-1/4 w-[400px] h-[400px] bg-[#85BCC7] rounded-full mix-blend-multiply filter blur-[100px] opacity-20 animate-blob"></div>
          <div className="absolute bottom-1/4 right-1/4 w-[400px] h-[400px] bg-indigo-200 rounded-full mix-blend-multiply filter blur-[100px] opacity-30 animate-blob animation-delay-2000"></div>
        </div>

        {/* メインカード */}
        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="relative z-10 w-full max-w-[480px] bg-white/80 backdrop-blur-xl rounded-3xl border border-white shadow-2xl overflow-hidden"
        >
          <div className="h-1.5 w-full bg-gradient-to-r from-[#85BCC7] to-slate-300"></div>
          
          <div className="p-8 md:p-12">
            <header className="text-center mb-10">
              <div className="inline-flex items-center justify-center w-12 h-12 rounded-full bg-[#85BCC7]/10 text-[#85BCC7] mb-6">
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 7a2 2 0 012 2m4 0a6 6 0 01-7.743 5.743L11 17H9v2H7v2H4a1 1 0 01-1-1v-2.586a1 1 0 01.293-.707l5.964-5.964A6 6 0 1121 9z" /></svg>
              </div>
              <h1 className="text-2xl font-bold text-slate-800 mb-2">パスワードの再設定</h1>
              <p className="text-sm text-slate-500 leading-relaxed">
                登録済みのメールアドレスを入力してください。<br/>
                リセット用のリンクをお送りします。
              </p>
            </header>

            <form onSubmit={handleSubmit} className="flex flex-col gap-6">
              <div className="space-y-2">
                <label htmlFor="email" className="text-xs font-bold text-slate-400 uppercase tracking-wider block ml-1">
                  Email Address
                </label>
                <div className="relative group">
                  <input 
                    type="email" 
                    id="email" 
                    placeholder="your-email@u.tsukuba.ac.jp" 
                    required
                    className="w-full bg-slate-50 border-2 border-slate-100 rounded-xl px-4 py-3.5 text-slate-700 font-medium placeholder-slate-300 focus:outline-none focus:border-[#85BCC7] focus:bg-white transition-all"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    pattern=".*@u\.tsukuba\.ac\.jp$"
                    disabled={status.type === 'loading' || status.type === 'success'}
                  />
                  <div className="absolute inset-y-0 right-0 flex items-center pr-4 pointer-events-none text-slate-400 group-focus-within:text-[#85BCC7] transition-colors">
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" /></svg>
                  </div>
                </div>
              </div>

              {/* ステータスメッセージ */}
              <AnimatePresence mode="wait">
                {status.type && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: 'auto' }}
                    exit={{ opacity: 0, height: 0 }}
                    className={`text-sm p-4 rounded-xl border flex items-start gap-3 ${
                      status.type === 'success' ? 'bg-emerald-50 text-emerald-800 border-emerald-100' :
                      status.type === 'error' ? 'bg-rose-50 text-rose-800 border-rose-100' :
                      'bg-blue-50 text-blue-800 border-blue-100'
                    }`}
                  >
                    <div className="mt-0.5 shrink-0">
                      {status.type === 'success' && <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" /></svg>}
                      {status.type === 'error' && <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" /></svg>}
                      {status.type === 'loading' && <div className="w-4 h-4 border-2 border-current border-t-transparent rounded-full animate-spin"></div>}
                    </div>
                    <span>{status.text}</span>
                  </motion.div>
                )}
              </AnimatePresence>

              <button 
                type="submit" 
                disabled={status.type === 'loading' || status.type === 'success' || !email}
                className="w-full bg-[#2C333A] text-white p-4 rounded-xl font-bold hover:bg-[#85BCC7] hover:shadow-lg hover:shadow-[#85BCC7]/30 transition-all duration-300 transform active:scale-[0.98] disabled:opacity-50 disabled:cursor-not-allowed mt-2"
              >
                {status.type === 'loading' ? '処理中...' : 'リセットリンクを送信'}
              </button>
            </form>

            <div className="mt-8 pt-8 border-t border-slate-100 text-center">
              <Link 
                href="/" 
                className="inline-flex items-center gap-2 text-sm text-slate-500 hover:text-[#85BCC7] font-medium transition-colors group"
              >
                <svg className="w-4 h-4 transform group-hover:-translate-x-1 transition-transform" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" /></svg>
                ログイン画面に戻る
              </Link>
            </div>
          </div>
        </motion.div>
      </div>
    </>
  );
}